.new {
    background-color: rgb(16, 177, 177);
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;
}

textarea {
    background-color: rgb(16, 177, 177);
    border: none;
    resize: none;
}

textarea:focus {
    outline:none;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.save {
    background-color: rgb(185, 183, 183);
    border: none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
}

.save:hover {
    background-color: rgb(158, 157, 157);
    cursor: pointer;
}