.note {
    background-color: #f7e650;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.footer {
    display: flex;
    align-items: center;
    justify-content: space-between;

}

.delete {
    cursor: pointer;
}
